body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100%;
  width: 100%;
  background: url(/static/media/bussiness-computer-dim.e93286d3.jpg) no-repeat fixed center center;
}
.app {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.contentContainer {
  position: relative;
  margin: 70px auto 10px;
  height: auto;
  width: 70vw;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  border: 3px solid white;
  color: white;
  text-align: center;
}
h1 {
  font-size: 50px;
}
h2 {
  margin: 20px;
  font-size: 30px;
}
p, a {
  font-size: 20px;
}
a {
  color: white;
}
@media (max-width: 920px) {
  .contentContainer {
    width: 90vw;
  }
  h1 {
    font-size: 40px;
  }
  h2 {
    font-size: 25px;
  }
  p, a {
    font-size: 15px;
  }
}

.navBarContainer {
	height: 90px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.navBarContainer a {
	font-size: 16px;
}
.logo {
	height: 90px;
	width: 90px;
	border-radius: 20px;
	border: 3px solid white;
	align-self: center;
	background: rgba(255, 255, 255, 0.1);
}
.desktopNav {
	margin-top: 35px;
	display: flex;
	flex-direction: row;
	height: 30px;
}
.desktopNav ul{
	height: 30px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.desktopNav ul li {
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	margin: 0 auto;
	list-style-type: none;
	padding: 0 20px 0 20px;
	margin: 0 30px 0 30px;
	color: white;
	border: 3px solid white;
	border-radius: 20px;
}
.desktopNav ul li a {
	text-decoration: none;
	color: white;
}
.phoneNav {
	display: none;
}
li { 
	transition: 0.3s;
 }
li:hover {

	background: rgba(255, 255, 255, 0.2);
}
@media (max-width: 950px) {
	.desktopNav {
		display: none;
	}
	.phoneAboutMe {
		border-left: 3px solid white;
	}
	.phoneNav {
		text-align: center;
		position: relative;
		margin: 0;
		width: 100%;
		display: block;
		background: rgba(255, 255, 255, 0.1);
		border-bottom: 3px solid white;
	}
	.phoneNav ul {
		position: relative;
		padding: 0;
		margin: 0;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.phoneNav ul li {
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0;
		flex: 1 1;
		height: 85px;
		list-style-type: none;
		color: white;
		border-right: 3px solid white;
	}
	.phoneNav ul li a {
		text-decoration: none;
		color: white;
	}

}
.aboutMe {
	text-align: center;
	color: white;
}
.paragraphContainer {
	margin: 0 40px 0 40px;
	border-top: 2px solid white;
}
.technologiesContainer {
	text-align: center;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	border-top: 2px solid white;
}

.iconContainer img {
	height: 100px;
	padding: 20px;
}

@media (max-width: 480px) {
	.iconContainer img {
		height: 75px;
		padding: 10px;
	}
}
.projectsContainer {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	margin: 40px;
}
.githubLink {
	border: 3px solid white;
	border-radius: 100%;
	height: 60px;
	width: 60px;
	cursor: pointer;
	transition: 0.3s;
}
.githubLink:hover {
	transition: 0.3s;
	background: white;
}
.projects {
	position: relative;
	height: 300px;
	width: 300px;
	border-radius: 20px;
	border: 3px solid white;
	overflow: hidden;
	margin: 0px 60px 40px;
}
.phoneSummary {
	display: none;
}
.robofriends {
	background-image: url(/static/media/Robofriends.021bb939.png);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.smartBrain {
	background-image: url(/static/media/Smartbrain.f8c7963f.png);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.resumeSite {
	background-image: url(/static/media/ResumeSite.44313597.PNG);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.rainbowAngles {
	background-image: url(/static/media/RainbowAngles.a004f4e0.PNG);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;	
}
.shadowBox {
	z-index: 2;
	background: rgba(0,0,0,0);
	color: rgba(0,0,0,0);
	position: absolute;
	top: 0;
	left: 0;
	height: 300px;
	width: 300px;
	border-radius: 20px;
	transition: 0.3s;
}
.shadowBox p {
	margin: 0 10px;
	position: relative;
	top: 50%;
	-webkit-transform: perspective(1px) translateY(-50%);
	        transform: perspective(1px) translateY(-50%);
}
.shadowBox:hover {
	z-index: 2;
	transition: 0.3s;
	background: rgba(0, 0, 0,0.9);
	display: inline-block;
	vertical-align: middle;
	color: white;
	height: 300px;
	width: 300px;
}
@media (max-width: 1332px) {
	.projects {
		margin: 0px 20px 40px;
	}
}
@media (max-width: 920px) {
	.projectsContainer {
		flex-direction: column;
	}
	.projects {
		margin: 0px 20px 20px;
		height: 200px;
		width: 200px;		
	}
	.projectBlockContainer {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.phoneSummary {
		display: inline-block;
		float: right;
		color: white;
		align-self: ;
	}
	.shadowBox {
		display: none;
	}
	.robofriends {
		height: 200px;
		width: 200px;
	}
	.smartBrain {
		height: 200px;
		width: 200px;
	}
	.resumeSite {
		height: 200px;
		width: 200px;
	}
	.rainbowAngles {
		height: 200px;
		width: 200px;
	}
}
@media  (max-width: 480px) {
	.projectsContainer {
		margin: 0;
	}
	.projects {
		margin: 10px;
		height: 150px;
		width: 150px;
	}
	.robofriends {
		height: 150px;
		width: 150px;
	}
	.smartBrain {
		height: 150px;
		width: 150px;
	}
	.resumeSite {
		height: 150px;
		width: 150px;
	}
	.rainbowAngles {
		height: 150px;
		width: 150px;
	}
}
.contactMe p, .contactMe a {
	margin-left: 10px;
	font-size: 20px;
}

.contactInfo {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
